
* {
  box-sizing: border-box;
}

*:before,
*:after,
*::before,
*::after {
  box-sizing: inherit;
}

::-moz-selection{
  background: #3A3E3E;
  color: #fff;
  text-shadow: none;
}

::selection{
  background: #3A3E3E;
  color: #fff;
  text-shadow: none;
}

html,
body {
  line-height: 1.5;
  margin: 0;
  -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
p {
  margin: 0;
}

hr {
  border: 0;
  border-top: 1px solid #e5e5e5;
  height: 5px;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 12px;
}

body {
  background: #fff;
  color: #333;
  font-family: Open Sans, sans-serif;
  font-weight: 300;
  overflow-x: hidden;
}

a {
  display: block;
  text-decoration: none;
  outline: 0;
}

.icon {
  font-size: 24px;
}

.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

aside {
  width: 100px;
  height: 100vh;
  text-align: center;
}

aside li {
  display: block;
}

aside li a {
  padding: 10px;
  text-decoration: none;
  color: #999;
}

.logo {
  margin: 5px;
}

main {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 10px;
}

.view {
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.view-enter, .view-leave {
  opacity: 0;
  -webkit-transform: translate3d(10px, 0, 0);
          transform: translate3d(10px, 0, 0);
}

.v-link-active {
  color: #333;
}

[v-cloak] {
  display: none;
}



